
.quick-pager {
  margin: 15px 20px;
}
.quick-pager-content {
  float: right;
  color: #5e5e5e;
}
.quick-pager-info {
  display: inline-block;
  height: 25px;
  float: left;
  margin-right: 15px;
  line-height: 25px;
}
.quick-pager-content ol {
  float: left;
}
.quick-pager-content li {
  float: left;
  cursor: pointer;
  height: 23px;
  line-height: 23px;
  padding: 0px 8px;
  margin-left: 5px;
  border: 1px solid gainsboro;
  text-align: center;
}
.quick-pager-content li.isCurrentPage {
  border-color: #06addd;
  background-color: #e7faff;
  color: #06addd;
}
.quick-pager-content .icon {
  font-size: 12px;
  color: #777777;
}
.quick-pager-content li.isValidIcon {
  cursor: not-allowed;
}
.quick-pager-content .isValidIcon i {
  color: #cdcdcd;
}



