
* {
  margin: 0px;
  padding: 0px;
}
.logo img {
  height: 60px;
  margin-left: 20px;
}
.search-box {
  margin-left: 10px;
  position: relative;
}
.search-box .icon-sousuo {
  cursor: pointer;
  position: absolute;
  top: 2px;
  right: 6px;
}
.left {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.header {
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-bottom: 1px solid #b7b7b7;
}
.header .header-right {
  font-weight: bold;
  margin-right: 50px;
  line-height: 46px;
  position: relative;
}
.header .header-right .person-info {
  width: 100px;
  background-color: #fff;
  color: #000;
  display: none;
  position: absolute;
  z-index: 10000;
}
.header .header-right .person-info p {
  text-align: center;
  cursor: pointer;
  border-bottom: 1px solid #b7b7b7;
}
.header .header-right .person-info p:hover {
  color: #37a1f9;
}
.header .header-right:hover .person-info {
  display: block;
}
.header .header-left {
  margin-left: 50px;
  font-weight: bold;
}
.header .header-left .searchCourse {
  padding-left: 5px;
  height: 26px;
  border-radius: 2px;
}
.header .header-left .item {
  width: 80px;
  height: 50px;
  line-height: 50px;
  display: inline-block;
  cursor: pointer;
  text-align: center;
}
.header .header-left .item:hover {
  color: #37a1f9;
  border-bottom: 3px solid #37a1f9;
}
.header-right {
  margin-top: 10px;
}
.header-right .item:nth-child(1) {
  width: 60px;
  height: 30px;
  line-height: 30px;
  display: inline-block;
  cursor: pointer;
  color: #fff;
  background-color: #37a1f9;
  border-radius: 5px;
  margin-right: 5px;
}
.header-right .item:nth-child(2) {
  width: 60px;
  height: 30px;
  line-height: 30px;
  display: inline-block;
  cursor: pointer;
}



