@hover-color:#37a1f9;
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre,
form, fieldset, legend, button, input, textarea, th, td {
  margin: 0;
  padding: 0;
}
ul, ol {
  list-style: none;
}
.item-selected{
  color: @hover-color;
  border-bottom: 3px solid @hover-color;
}


